.catalog-section{padding:30px 0}.section-title{font-size:24px;font-weight:600;margin-bottom:20px;text-align:center}.catalog-slider{position:relative}.catalog-slider :global(.swiper-button-next),.catalog-slider :global(.swiper-button-prev){color:#000;background-color:hsla(0,0%,100%,.8);width:40px;height:40px;border-radius:50%;box-shadow:0 2px 5px rgba(0,0,0,.1)}.catalog-slider :global(.swiper-button-next):after,.catalog-slider :global(.swiper-button-prev):after{font-size:18px}.catalog-slider :global(.swiper-button-disabled){opacity:.3}.swiper-container{margin:0;padding:10px 0;overflow:visible}.catalog-item{width:auto;transition:transform .3s ease}.catalog-item:hover{transform:translateY(-5px)}.catalog-item:hover .catalog-image{transform:scale(1.05)}.catalog-link{display:block;text-decoration:none;color:inherit}.catalog-image-wrapper{position:relative;overflow:hidden;border-radius:8px;aspect-ratio:3/4;margin-bottom:10px}.catalog-image{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;transition:transform .3s ease}.catalog-title{font-size:16px;font-weight:500;text-align:center;margin-top:10px}@media(max-width: 768px){.catalog-title{font-size:14px}.section-title{font-size:20px;margin-bottom:15px}.catalog-section{padding:20px 0}}@media(max-width: 480px){.section-title{font-size:18px}:global(.swiper-button-next),:global(.swiper-button-prev){display:none}}@media(max-width: 400px){.section-title{font-size:16px;margin-bottom:12px}.catalog-title{font-size:13px}.catalog-section{padding:15px 0}.catalog-image-wrapper{margin-bottom:5px}}